.header_background {
  z-index: -2;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 300px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3b80ff), to(#131313));
  background-image: -webkit-linear-gradient(top, #3b80ff, #131313);
  background-image: -o-linear-gradient(top, #3b80ff, #131313);
  background-image: linear-gradient(180deg, #3b80ff, #131313);
  -webkit-transform: skewY(0deg);
      -ms-transform: skewY(0deg);
          transform: skewY(0deg);
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
}

.header_title {
  text-align: center;
  color: #fff;
}


